import React from 'react';

const TableSection = ()=>{
    return(
        <table class="table" style={{width:"90%"}}>
          <thead>
            <tr>
              <th scope="col">Tecnologias</th>
              <th scope="col">Rol</th>
              <th scope="col">Entorno</th>
            </tr>
          </thead>
          <tbody>
            <tr>
              <th scope="row">Java</th>
              <td>Backend</td>
              
              <td>Eclipse</td>
            </tr>
            <tr>
              <th scope="row">Spring boot</th>
              <td>Backend</td>
             
              <td>Eclipse</td>
            </tr>
            <tr>
              <th scope="row">React</th>
              <td>Frontend</td>
            
              <td>VSC</td>
            </tr>
            <tr>
              <th scope="row">Javascript</th>
              <td >Frontend</td>
              
              <td>Eclipse y VSC</td>
            </tr>
            <tr>
              <th scope="row">html5</th>
              <td>Frontend</td>
              
              <td>Eclipse y VSC</td>
            </tr>
          </tbody>
       </table>
    );
}
export default TableSection;