import React from 'react';
import CardsSection from './CardsSection';
import {targeta} from '../../Api/inicioCards';
import fondo from '../../medias/images/desktop-1245714.jpg';
import TableSection from './TableSection';
import Grafica from './Grafica';
import {useAnimator} from '../../hooks/useAnimator';
const Section = ()=>{
    useAnimator(".an",-900);
    return(
        <>
        <section class="hero" style={{textAlign:"center"}}>
            <div class="hero-body">
                <p class="title">
                Contenido
                </p>
                <hr style={{border:"1px solid #00FF00",width:"10%",margin:"auto"}}/>
                <br/>
                <div class="columns is-desktop">
                    {targeta.map((el)=>
                        (
                            <div class="column">
                                <CardsSection  imagen={el.img}
                                    titulo={el.title} 
                                    descripcion={el.descripcion}
                                    horasTotales={el.horasTotales}
                                  />
                            </div>
                        )
                    )}

                </div>
                <br/><br/><br/>

                <div class="columns is-desktop"
                    style={{marginLeft:"-1.5em"
                    ,marginRight:"-1.5em"}}>

                    <div class="column"
                        style={{backgroundImage:`url(${fondo})`
                        ,height:"40vh"
                        ,width:"100%"
                        ,backgroundAttachment:"fixed"
                        ,backgroundSize:"cover"
                        ,overflow:"hidden"}}>
                            <br/><br/>
                            <p className="title an" style={{color:"white"}}>Datos e información relevante</p>
                    </div>
                </div>
                <br/>
                <br/>
                    <br/>
                    <br/>
                    <hr/>
                <div class="columns is-desktop">
                    <div class="column " style={{textAlign:"center"}}>
                        <h1
                            style={{fontSize:"3vh",display:"block"}}>
                            Lenguajes mas usados del 2021
                        </h1>
                        <Grafica/> 
                    </div>
                    <div class="column ">
                        <h1 
                            style={{fontSize:"3vh"}}>
                            Estadistica del curso
                        </h1>
                        <TableSection/>
                    </div>
                </div>
                <hr/>
            </div>
            <p style={{width:"2px"
                       ,height:"2px"
                       ,position:"fixed"
                       ,bottom:"0"
                       ,background:"red"}}
                       className="RefS"
                       ></p>
            <p style={{width:"2px"
                       ,height:"2px"
                       ,position:"fixed"
                       ,top:"0"
                       ,background:"red"}}
                       className="RefI"
                       ></p>
        </section>
        </>
    );
}
export default Section;