import React from 'react';
import image from '../../medias/images/cyborg-88.png';
import '../../css/title.css';
import {useAnimatorLoad} from '../../hooks/useAnimator';
const Header = ()=>{
    useAnimatorLoad(".anm")
    return(
        <section class="hero is-primary">
            <br/>
            <div class="hero-body">
                <div class="columns is-desktop">
                    <div class="column">
                        <p class="title anm">
                            Bienvenido a MS Academy donde aprendera desarrollo web fullstack de forma Gratuita
                        </p>
                        <br/>
                        <p class="subtitle">
                            <div class="control">
                                <div class="tags has-addons">
                                    <a class="tag is-link">Maynor Alvarez Acopa</a>
                                    <a class="tag">
                                        <img src="https://img.icons8.com/plasticine/20/000000/bookmark--v1.png" />
                                    </a>
                                </div>
                            </div>
                        </p>
                    </div>
                    <div class="column" style={{textAlign:"center"}}>
                        <img src={image} style={{width:"25vh",transform:"scale(1.8)"}}/>
                    </div>
                </div>
            </div>
        </section>
    );
}
export default Header;