import React,{useRef} from 'react';
import Bulma from '@vizuaalog/bulmajs';
import {useNavFixed} from '../../hooks/useNavFixed';
const NavBar = ({Link})=>{
  const message = useRef()
    useNavFixed(".fix")
    const handldeMessage = (e)=>{
      console.log("active")
     message.current.style.display="block"

    }
    const handldeClose = (e)=>{
      message.current.style.display="none"

    }
    return(
      <nav className="navbar is-primary fix" style={{width:"vw"}}>
        <div className="navbar-brand">
          <Link className="navbar-item" to="/">
              <img src="data:image/svg+xml;base64," />
              {'\u00A0'}
              Portafolio
          </Link>

          <div className="navbar-burger" data-target="navbarExampleTransparentExample">
            <span></span>
            <span></span>
            <span></span>
          </div>
        </div>

        <div id="navbarExampleTransparentExample" className="navbar-menu">
          <div className="navbar-start">
            <a className="navbar-item" href="#" style={{position:"relative"}} onClick={handldeMessage}>
            <img style={{width:"20px",heigth:"20px"}} src="https://img.icons8.com/cute-clipart/64/000000/book.png"/>
            {"\u00A0"}
              Mini cursos
            <div ref={message} className="notification is-danger" style={{bottom:"0px",zIndex:"1",display:"none"}} >
              <button className="delete" onClick={handldeClose}></button>
              Este apartado aun no esta diponible
            </div>
            </a>
            <Link to="/cv" className="navbar-item">
            <img src="https://img.icons8.com/office/24/000000/parse-from-clipboard.png"/>
              {"\u00A0"}
                cv
            </Link>
            <div className="navbar-item has-dropdown is-hoverable">
              <a className="navbar-link" href="#">
              <img src="https://img.icons8.com/dusk/20/000000/doc.png"/>
              {"\u00A0"}
                Docs
              </a>
              <div className="navbar-dropdown is-boxed">
                <a className="navbar-item" href="https://github.com/Maynor2000" target="_blank" rel="noopener">
                <img src="https://img.icons8.com/windows/32/000000/github.png"/>{"\u00A0"}Mi github
                </a>
                <a className="navbar-item" href="https://www.facebook.com/mayk.akopii.7/" target="_blank" rel="noopener">
                <img src="https://img.icons8.com/nolan/64/facebook.png"/>{"\u00A0"}Facebook
                </a>
              </div>
            </div>
          </div>

          <div className="navbar-end">
            <div className="navbar-item">
              <div className="field is-grouped">
                <p className="control">
                  <button className="bd-tw-button button" title="proximamente">
                    <span className="icon">
                      <i className="fab fa-twitter"></i>
                    </span>
                    <span>
                      MS Desing Company
                    </span>
                  </button>
                </p>
                <p className="control" style={{width:"40%"}}>
                  <a style={{width:"100%"}} className="button button is-warning is-focused" href="https://github.com/Maynor2000/portafolio/raw/0c8e0984731b247f87b04af06387bb366390e310/src/medias/images/Maynor%20Alvarez%20Acopa%20cv.pdf">
                    <span className="icon">
                      <i className="fas fa-download"></i>
                    </span>
                    <span>Descargar C.V</span>
                  </a>
                </p>
              </div>
            </div>
          </div>
        </div>

      </nav>   
    )
}
export default NavBar;