import React from 'react';
import Load from '../../svg-loaders/audio.svg';
const Loader = ()=>{
    return(
        <div class="modal" style={{display:"block",position:"absolute",width:"100%",height:"100%"}}>
            <div class="modal-background"></div>
                <div class="modal-content">
                    <p class="image is-4by3">
                        <img src={Load} alt="" style={{width:"10vh",margin:"auto"}}/>
                    </p>
                </div>
        </div>
    );
}
export default Loader;