import React,{lazy,Suspense} from 'react';
import NavBar from './components/nav/nav.bar';
import App2 from './components2/app2';
import Loader from './components2/section/Loader';
import {
  BrowserRouter as Router,
  Switch,
  Route,
  Link
} from "react-router-dom";

const App1 = lazy(()=>{
  return import('./components/app1');
})

function App(props) {
  return (
    <>
    <Router>
    <Link to="/portafolio">
    </Link>
    
    <Switch>
      <Route path="/cv">
        <NavBar Link={Link}/>
        <Suspense fallback={<Loader/>}>
          <App1/>
        </Suspense>
      </Route>
      <Route path="/portafolio">
        <NavBar Link={Link}/>
        <App2/>
      </Route>


    </Switch>
    </Router>
    </>
  );
}

export default App;
