import React from 'react';

const CardsSection = (props)=>{
    return(
        <div class="card" style={{overflow:"hidden",border:"1px solid black"}}>
        <div class="card-image">
          <figure class="image is-4by3">
          <img src={props.imagen}/>
          </figure>
        </div>
        <div class="card-content">
          <div class="media">
            <div class="media-content">
              <p class="title is-4">{props.titulo}</p>
            </div>
          </div>
          <div class="content">
                {props.descripcion}
            <br/>
            <time >{props.horasTotales}</time>
          </div>
        </div>
      </div>  
    );

}

export default CardsSection;